"use client"

import { useParams } from "next/navigation"
import ProductCard from "@/app/components/After_log_homepage/ProductCard"
import { mockProducts } from "@/app/data/mockProducts"

export default function SubcategoryPage() {
  const params = useParams() // Fetch params dynamically
  const category = params.category as string
  const subcategory = params.subcategory as string

  const filteredProducts = mockProducts.filter(
    (product) => product.category === category && product.subcategory === subcategory
  )

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl max-w-7xl mx-auto font-bold mb-6 capitalize">{subcategory.replace("-", " ")}</h1>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
        {filteredProducts.length > 0 ? (
          filteredProducts.map((product) => (
            <ProductCard key={product.id} {...product} />
          ))
        ) : (
          <p>No products found.</p>
        )}
      </div>
    </div>
  )
}
