import Link from "next/link";
import Image from "next/image";

interface ProductCardProps {
  id: string;
  name: string;
  image: string;
  price: number;
  category: string;
  subcategory: string;
}

export default function ProductCard({ id, name, image, price, category, subcategory }: ProductCardProps) {
  return (
    <Link href={`/shop/${category}/${subcategory}/${id}`} className="block max-w-7xl mx-auto bg-white p-4 w-[60%] rounded-lg border shadow-sm hover:shadow-md transition">
      {/* Product Image */}
      <div className="relative  h-48 mb-4 group">
        <Image
          src={image || "/placeholder.svg"}
          alt={name}
          layout="fill"
          objectFit="cover"
          className="rounded-md transition-opacity group-hover:opacity-80"
        />
      </div>
      
      {/* Product Info */}
      <div className="space-y-2 text-center">
        <h3 className="font-medium text-lg">{name}</h3>
        <p className="text-lg font-semibold">${price.toFixed(2)}</p>
      </div>
    </Link>
  );
}
